.backFon{
  width: 100vw;
  height: 100vh;
  background: linear-gradient(14deg, rgba(237,232,139,1) 0%, rgba(229,232,178,1) 48%, rgba(235,240,181,1) 100%);
}

.contenido{
  z-index: 3;
  border-radius: 50px;
  box-shadow: 0px 0px 10px 0px #000;
  padding: 0px 20px;
  background: linear-gradient(14deg, rgba(139,117,74,1) 0%, rgba(163,145,111,1) 52%, rgba(141,119,76,1) 100%);
}

.fondoOpci{
  background: rgba(20, 11, 11, 0.349);
  color: #fff;
}

.logoFondo{
  position: absolute;
  opacity: .2;
  z-index: -1;
  top: 0%;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
}



@media screen and (max-width: 500px) {
  .contenido{
    border-radius: 0;
  }
}




.contenedor {
  height: 85%;
  overflow-y:auto;
  overflow-x: hidden;
}

/* Estilos para motores Webkit y blink (Chrome, Safari, Opera... )*/

.contenedor::-webkit-scrollbar {
  -webkit-appearance: none;
}

.contenedor::-webkit-scrollbar:vertical {
  width:10px;
}

.contenedor::-webkit-scrollbar-button:increment,.contenedor::-webkit-scrollbar-button {
  display: none;
} 

.contenedor::-webkit-scrollbar:horizontal {
  height: 10px;
}

.contenedor::-webkit-scrollbar-thumb {
  background-color: #e79140;
  border-radius: 20px;
  border: 1px solid #000000;
}

.contenedor::-webkit-scrollbar-track {
  border-radius: 10px; 
}